.formGroup {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    width: 90%;
    margin-left: 1rem;
    padding: 0 0.5rem;
}
.formGroup:first-child{
    margin-top: 2rem;
}
label{
    font-size: 18px;
}
.formGroup input{
    outline: none;
    padding: 0.3rem;
}
.inputField input{
    min-width: 100%;
}
.selectField select, textarea{
    min-width: 100%;
    outline: none;
    padding: 0.4rem;
}
.submitBtn{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    padding: 1.5rem;
}
.submitBtn button{
    width: 200px;
    padding: 5px;
    border-radius: 5px;
}